.header-upload {
	padding-top: 25px;
	margin-bottom: 30px;
}

input {
	text-align: center;
}

.btn-file {
	position: relative;
	overflow: hidden;
}

input {
	margin: 0 auto;
	max-width: 100%;
}

.btn-file input[type="file"] {
	position: absolute;
	top: 0;
	right: 0;
	min-width: 100%;
	min-height: 100%;
	font-size: 100px;
	text-align: center;
	opacity: 0;
	outline: none;
	background: white;
	cursor: inherit;
	display: block;
}

.test-page-container {
	position: relative;
}
.test-page-container div {
	margin-top: 5px;
}

