	.block-header {
		margin: 0 60px;
		margin-top: 30px;
		margin-bottom: 40px;
		text-align: left;
		text-align: center;
	}
	.block-p {
		margin: 0 40px;
		margin-bottom: 15px;
		text-align: justify;
	}
	#image {
		height: 200px;
		width: auto;
	}

	.label-name {
		margin-top: 40px;
	}
}

@media only screen and (max-width: 800px) {
	#image {
		height: 220px;
		width: auto;
	}
}

.box-profile {
	margin-top: 20px;
}

.Container {
	margin: 0;
}

.delete-button {
	padding: 5px;
	border: none;
}

.FormKeyWords {
	display: flex;
	justify-content: center;
	background-image: url("../images/kitchen.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	position: relative;
	width: 100vw;
	height: 93vh;
}

.list {
	display: flex;
	justify-content: center;
	align-content: center;
}

.Keyword-list {
	width: 300px;
	display: flex;
	flex-direction: column;
	list-style-type: none;
	padding: 0;
	margin: 0;
}

.Form {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.Keyword {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 5px;
	font-weight: 700;
	color: rgb(54, 58, 64);
}

.keyword-input {
	border: solid 1px black;
	margin: 10px;
}

.keyword-btn {
	background-color: rgb(53, 58, 64);
	border: none;
	margin: 10px;
}

.product-container {
	padding: 20px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
}

.product {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	width: 30%;
	margin-bottom: 30px;
	background-color: rgb(243, 244, 245);
	border-radius: 4px;
	box-shadow: 0.5px 1px rgb(148, 148, 148);
}

.imageWrapper {
	width: 80%;
	height: 40%;
}
.imageWrapper a {
	width: 100%;
	height: 100%;
}
.product-pic {
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.product-star {
	display: flex;
	justify-content: flex-start;
	height: 30px;
}

.productInfoWrapper p {
	margin-bottom: 0;
}

.productInfoWrapper a h5 {
	color: rgb(53, 58, 64);
	text-decoration: none !important;
}

.productInfoWrapper a h5:hover {
	text-decoration: none !important;
}

.button {
	background-color: rgb(53, 58, 64);
	border: none;
	padding-top: 5px;
	margin: 5px;
}

@media only screen and (max-device-width: 375px) and (max-device-height: 812px) {
	/* Styles */
	.product {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		width: 100%;
		margin-bottom: 30px;
		background-color: rgb(243, 244, 245);
		border-radius: 4px;
		box-shadow: 0.5px 1px rgb(148, 148, 148);
	}
}

